import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home/Home'
import Detail from '@/components/detail/Detail'
import News from '@/components/news/News'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }, {
      path: '/detail',
      name: 'Detail',
      component: Detail
    }, {
      path: '/news',
      name: 'News',
      component: News
    }
  ]
})



