<template>
  <div class="wrapper">
      <swiper :options="swiperOption" v-if="showSwiper">
          <swiper-slide v-for="item of swiperSlides" :key="item.id">
            <img :src="item.imgUrl" class="swiper-img" />
            <p class="item-title">{{item.title}}</p>
            <p class="item-desc">{{item.desc}}</p>
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeSwiper',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        slidesPerView: 1,
        spaceBetween: 30,
        autoplay: {
          disableOnInteraction: false
        },
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
        // navigation: {
        //   nextEl: '.swiper-button-next',
        //   prevEl: '.swiper-button-prev'
        // }
      },
      swiperSlides: [{
        id: '0001',
        imgUrl: './static/images/01carousel.png',
        title: '航拍无人机',
        desc: 'X8科研优势立足哈尔滨工业大学，并持续融入自身的创造力和想象力，经过相关专家的多次技术论证和实践检验'
      }, {
        id: '0002',
        imgUrl: './static/images/02carousel.png',
        title: '植保无人机',
        desc: 'X8突破常规四旋翼结构，率先采用四轴八桨结构，使其在四轴的形态下能达到八轴的稳定性，具备断桨保护功能，在缺失桨叶的情况下仍可安全返航'
      }]
    }
  },
  computed: {
    showSwiper () {
      return this.swiperSlides.length
    }
  }
}
</script>

<style lang="stylus" scoped>
  .wrapper >>> .swiper-pagination-bullets
    margin-top: 30px
  .wrapper >>> .swiper-pagination-bullet-active
    background: #aaa
  .wrapper >>> .swiper-button-prev,.swiper-button-next
    color: #666
    height: 15px
    margin-top: -13px
  .wrapper >>> .swiper-pagination-bullet
    background-color: #aaa
  .wrapper
    overflow: hidden
    background-color: #fff
    height: 200px
    margin-top: 20px
    .swiper-container
      overflow: hidden
    .swiper-pagination
      position: relative
    .swiper-img
      width: 160px
      height: 60px
    .item-title
      color: #444
      font-font-family: "NeoSansStd-Regular", "微软雅黑"
      font-size: 16px
      height: 42px
      line-height: 46px
    .item-desc
      color: #846E6E
      height: 42px
      line-height: 46px
      font-size: 14px
</style>



