    <span class="blank" style="height:20px;"></span>
    <span class="blank" style="height:20px;"></span>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
  .header
    display: flex
    background: $bgColor
    color: #fff
    position: fixed
    top: 0
    z-index: 9999
    width: 100%
    height: 60px
    .header-left
      width: 60px
      height: 60px
      float: left
      .back-icon
