<template>
  <div>
    <div class="wrapper">
      <swiper :options="swiperOption" v-if="showSwiper">
          <swiper-slide v-for="item of swiperSlides" :key="item.id">
            <img :src="item.imgUrl" class="swiper-img" />
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
          <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
          <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
      </swiper>
    </div>
    <div class="header">
      <p class="title">中山华奥易航科技有限公司</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeImage',
  props: {
    list: Array
  },
  data () {
    return {
      swiperOption: {
        // slidesPerView: 1,
        spaceBetween: 30,
        effect: 'fade',
        // autoplay: {
        //   disableOnInteraction: false
        // },
        loop: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      swiperSlides: [{
        id: '0001',
        imgUrl: './static/images/home1.png'
      }, {
        id: '0002',
        imgUrl: './static/images/home2.png'
      }, {
        id: '0003',
        imgUrl: './static/images/home3.jpg'
      }]
    }
  },
  computed: {
    showSwiper () {
      return this.swiperSlides.length
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
  .wrapper >>> .swiper-pagination-bullet,.swiper-pagination-bullet-active
    background-color: #aaa
  .swiper-img
    width: 100%
    height: 490px
    margin-top: 60px
  .header
    width: 100%
    background-color: $bgColor
    padding: 36px 0 30px 0
    .title
      color: #f3f3f3
      font-font-family: "NeoSansStd-Medium"
      text-align: center
      font-size: 21px
</style>



